export default {
  data() {
    return {
      recipe_data: {}
    };
  },
  async asyncData({params }) {
    const recipe_id = params.id
    return { recipe_id }
  },
  async fetch() {
    this.recipe_data =  await this.$axios.get('http://erised:3333/api/recipes/' + this.recipe_id).then((res) => res.data).catch((error) => console.log(error));
  }
}
